import React from "react";

const GharKaMart = () => {
  const categories = [
    { name: "Fruits & Vegetables", img: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcS-pM2cVm5aP1LOpQmwK2y41Ohv81XUmeds5g&s" },
    { name: "Dairy & Bakery", img: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcT4kshRrqL695uhLvxoecsJcWJ9FlBPj3Y-wQ&s" },
    { name: "Snacks & Beverages", img: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQZiZlFWsbxynzv0yRpog_iV3p0aiH-i8bnDw&s" },
    { name: "Household Essentials", img: "data:image/jpeg;base64," },
    { name: "Personal Care", img: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcQYUW14IqCacxZB21EyOdgr3UmJ74Z1G0S4-w&s" },
    { name: "Spices & Condiments", img: "https://hmhub.in/wp-content/uploads/2019/09/1440x960-spices.jpg" },
  ];

  return (
    <div className="mart-container">
      {/* Hero Section */}
      <section className="hero">
        <div className="hero-content">
          <h1>Welcome to GharKaMart</h1>
          <p>Your trusted partner for all household needs!</p>
          <button className="cta-button">Shop Now</button>
        </div>
      </section>

      {/* Categories Section */}
      <section className="categories">
        <h2>Shop by Categories</h2>
        <div className="category-grid">
          {categories.map((category, index) => (
            <div key={index} className="category-card">
              <div className="category-image">
                <img src={category.img} alt={category.name} />
              </div>
              <h3>{category.name}</h3>
            </div>
          ))}
        </div>
      </section>

      {/* Footer */}
      <footer className="mart-footer">
        <p>¬© 2024 GharKaMart | Made with ‚ù§Ô∏è for your home</p>
      </footer>
    </div>
  );
};

export default GharKaMart;
